import React from 'react'
import { Link } from 'react-router-dom'
import Button from '@mui/material/Button';



function Dashboard() {
  return (
    <div>
      <img width="600px" src='https://www.zohowebstatic.com/sites/default/files/catalyst/catalyst_authenticationapp_cli_login_success_browser.jpg' />

      <br/>
      <Button
              type="submit"
              className='btn'
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
      <Link to="/signup" >Go To Home</Link>
             
            </Button>


    </div>
  )
}

export default Dashboard